/* Modal Button */
.modalButton2 {
  position: fixed;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
  padding: 12px 24px;
}


/* Modal */
.overlay2 {
  /* background-color: rgba(0, 0, 0, 0.5); */
  position: fixed;
  width: 100%;
  height: 100%;
}

.modalContainer2 {
  max-width: 750px;
  width: 100%;
  position: fixed;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  background-color: #ffffff;
  box-shadow: 0px 0px 18px 0px rgba(0, 0, 0, 0.75);
  border-radius: 8px;
}


.modalRight2 {
  width: 100%;
}

.closeBtn2 {
  position: fixed;
  top: 8px;
  right: 8px;
}

.content2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  margin-top: 3rem;
  padding: 1rem 2rem;
}

.modal-body2 {
  overflow-y: auto;
}

.btnContainer2 {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4rem 1rem 0.5rem 1rem;
}

.btnContainer2 button {
  width: 50%;
  margin: .5rem;
  padding: 5px 0;
  /* border: none; */
  border: 1px solid #411b57;
  /* color: #ffffff; */
}

.btnPrimary2 {
  background-color: #411b57;
  color: white;
}

.btnOutline2 {
  /* background-color: #a76a99; */
  background-color: white;
  color: #411b57;
}

.bold2 {
  font-weight: 600;
}

@media screen and (max-width: 500px) {
  .modalContainer2 {
    flex-direction: column;
    top: 0;
    left: 0;
    transform: none;
    width: 100%;
    height: 100vh;
  }

  img {
    width: 100%;
    max-height: 70vh;
    object-fit: cover;
  }

  .heading2 {
    margin: 1rem;
  }

}
